.emailRow {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 50px;
  border-bottom: 1px solid whitesmoke;
  cursor: pointer;
}

.emailRow:hover {
  border-top: 1px solid white;
  box-shadow: 0px 4px 4px -2px rgba(0, 0, 0, 0.24);
}

.emailRow_title {
  font-size: 13px;
  flex: 0.1;
  font-weight: bold;
  padding-right: 10px;
}

.emailRow_message {
  flex: 0.8;
  align-items: center;
  font-size: 13px;
  display: flex;
}

.emailRow_message h4 {
  width: 400px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: flex;
}

.emailRow_description {
  font-weight: lighter;
  padding-left: 4px;
  color: gray;
}

.emailRow_time {
  font-size: 13px;
  flex: 0.1;
  font-weight: bold;
  margin-right: 20px;
}

.clickable {
  display: flex;
  flex: auto;
}

.emailRow_options {
  min-width: 140px;
}

.readMark {
  background-color: rgba(0, 0, 0, 0.05);
}
